  left: -999999px;
  visibility: hidden;
}

.nav_menu label {
  cursor: pointer;
}

.nav_menu_box {
  margin-top: -1px;
  padding: 32px 82px 32px 0;
  background: #ffffff;
  position: absolute;
  top: 76px;
  transition: right 0.6s;
  display: flex;
  flex-direction: column;
  z-index: 100;
  display: none;
}

.nav_menu_box_title {
  margin: 0 0 4px 33px;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
}

.nav_menu_box_heading {
  margin: 20px 0 1px 32px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #F16D7F;
}

.nav_menu_box_heading:hover {
  color: #000000;
}

.nav_menu_box_links {
  display: flex;
  flex-direction: column;
  gap: 11px;
  margin-top: 12px;
}

.nav_menu_box_links_item {
  margin: 0 0 0 54px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #6f6e6e;
}

.nav_menu_box_links_item:hover {
  color: #F16D7F;
}

.visibility {
  display: block;
}

.cart {
  position: relative;
}

.cartCount::after {
  position: absolute;
  top: -8px;
  right: -9px;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 19px;
  height: 19px;
  border-radius: 8.5px;
  background-color: #F16D7F;
  color: #ffffff;
  content: attr(data-count);
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
}

.catalogSlider {
  display: grid;
  grid-template-columns: repeat(3, 360px);
  gap: 30px;
}

.hidden {
  display: none;
}

.product-box_title {
